*
{
    font-family: 
    'Balsamiq Sans', 
    sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.btn-primary {
    border-radius: 25px;
    background-color: #2e3a32;
    border-color: #2e3a32;
}
.btn-primary:hover {
    font-weight: 500;
    box-shadow: 1px 1px 3px black;
}
.btn-primary:active {
    font-weight: 500;
    box-shadow: 1px 1px 4px black;
}
.btn-secondary {
    color: black;
    background-color: white;
    border-radius: 25px;
    border-radius: 25px;
    background-color: white;
    border-color: #2e3a32;

}
.btn-secondary:hover {
  background-color: #282c34;
}
